@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
body {
  margin: 0;
  color: #000000;
  background-color: #f2f2f2;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
.page-container {
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
}
.header {
  background-color: #ffffff;
  min-height: 65px;
  padding: 10px 20px 11px 20px;
  position: relative;
}
.header__block {
  display: flex;
  flex-wrap: wrap;
  min-height: 38px;
  width: 280px;
}
.header__logo {
  width: 34px;
  height: 38px;
  background-image: url("../../img/logo-mobile.svg");
  background-repeat: no-repeat;
}
.header__title {
  width: 101px;
  height: 18px;
  background-image: url("../../img/header-title.svg");
  background-repeat: no-repeat;
  margin: 13px 0 0 57px;
}
.header__nav {
  position: absolute;
  width: 320px;
  min-height: 264px;
  z-index: 1;
  top: 65px;
  left: 0;
}
.header__nav--nojs {
  position: static;
  min-height: 0;
}
.header__nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: #ffffff;
  display: none;
}
.header__nav-item {
  position: relative;
}
.header__nav-item:nth-child(2) .header__nav-link {
  padding: 22px 70px 24px 69px;
}
.header__nav-item:nth-child(3) .header__nav-link-last {
  padding: 22px 70px 24px 70px;
}
.header-nav--opened .header__nav-list {
  display: block;
}
.header__nav-link {
  display: block;
  width: 100%;
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  min-height: 65px;
  text-align: center;
  padding: 22px 122px 24px 122px;
}
.header__nav-link::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
  left: 0;
  top: 0;
}
.header__nav-link-last {
  display: block;
  width: 100%;
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  min-height: 65px;
  text-align: center;
  padding: 22px 122px 24px 122px;
}
.header__nav-link-last::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
  left: 0;
  top: 0;
}
.header__nav-link-last::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
  left: 0;
  bottom: 0;
}
.header__button {
  position: absolute;
  top: -43px;
  right: 20px;
  width: 24px;
  height: 18px;
  border: none;
  background-color: #ffffff;
  display: block;
  z-index: 2;
  background-image: url("../../img/button-closed.svg");
  background-repeat: no-repeat;
}
.header-nav--opened .header__button {
  background-image: url("../../img/cross-mobile.svg");
  background-repeat: no-repeat;
  height: 20px;
}
.header__nav--nojs .header__button {
  display: none;
}
.header__nav--nojs .header__nav-list {
  display: block;
}
.nutrition {
  position: relative;
  margin-bottom: 20px;
}
.nutrition__block {
  width: 320px;
  min-height: 302px;
  background-image: url("../../img/cat-background.jpg");
  background-repeat: no-repeat;
  padding: 27px 35px 0 36px;
}
.nutrition__title {
  width: 250px;
  min-height: 72px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  margin: 0 0 25px 0;
}
.nutrition__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #ffffff;
  text-transform: uppercase;
  margin: 0 0 0 24px;
}
.nutrition__can {
  position: absolute;
  top: 140px;
  left: 21px;
  margin-top: 27px;
}
.nutrition__button {
  display: block;
  text-decoration: none;
  width: 280px;
  min-height: 40px;
  background-color: #68b738;
  border: none;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: #ffffff;
  margin: 138px 20px 20px 20px;
  padding: 11px 73px 11px 74px;
}
.nutrition__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.nutrition__item {
  position: relative;
  width: 280px;
  min-height: 266px;
  background-color: #f2f2f2;
  margin: 0 20px 20px 20px;
  padding: 20px 0 0 0;
}
.nutrition__line {
  display: flex;
  flex-wrap: wrap;
}
.nutrition__cat-icon {
  margin: 0 0 0 22px;
}
.nutrition__item-title {
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  margin: 9px 0 0 19px;
}
.nutrition__item-title-last {
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  margin: 11px 0 0 10px;
}
.nutrition__item-text {
  display: block;
  width: 227px;
  min-height: 90px;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  margin: 24px 20px 0 20px;
}
.nutrition__item-text::before {
  content: "";
  position: absolute;
  width: 240px;
  height: 1px;
  background-color: #d9d9d9;
  top: 208px;
}
.nutrition__link {
  display: flex;
  flex-wrap: wrap;
  width: 134px;
  min-height: 24px;
  margin: 41px 0 0 20px;
  text-decoration: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
  background-image: url("../../img/right-arrow.svg");
  background-repeat: no-repeat;
  background-position: 102px 2px;
}
.nutrition__link-pro {
  display: flex;
  flex-wrap: wrap;
  width: 134px;
  min-height: 24px;
  margin: 41px 0 0 20px;
  text-decoration: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
  background-image: url("../../img/right-arrow.svg");
  background-repeat: no-repeat;
  background-position: 97px 2px;
}
.nutrition__panther-icon {
  margin: 3px 0 0 14px;
}
.advantages {
  padding: 0 20px;
}
.advantages__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  margin: 0 0 40px 0;
  width: 225px;
}
.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.advantages__item {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
.advantages__block-leaf {
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-image: url("../../img/icon-leaf-mobile.svg");
  background-repeat: no-repeat;
  background-position: 15px 14px;
}
.advantages__block-powder {
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-image: url("../../img/icon-powder-mobile.svg");
  background-repeat: no-repeat;
  background-position: 18px 16px;
}
.advantages__block-cutlery {
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-image: url("../../img/icon-cutlery-mobile.svg");
  background-repeat: no-repeat;
  background-position: 18px 14px;
}
.advantages__block-clock {
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-image: url("../../img/icon-clock-mobile.svg");
  background-repeat: no-repeat;
  background-position: 18px 14px;
}
.advantages__text {
  display: block;
  width: 193px;
  min-height: 54px;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  margin: 2px 0 0 21px;
}
.example {
  padding: 25px 20px 0 20px;
  background-color: #eaeaea;
}
.example__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  margin: 0 0 40px 0;
}
.example__text {
  display: block;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
